<script>
import moment from 'moment'
import { foods } from  '@/constants/foods.js'

export default {
  props: {
    active: {
      type: Number,
      required: true,
    },
    orders: {
      type: Array,
    },
    language: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      type: 'Dine-in',
      date: null,
      time: null,
      name: '',
      numOfPpl: 0,
      languagePreference: 'Chinese',

    }
  },
  methods: {
    haveInvalidField() {
      if (this.date == null) return true
      if (this.time == null) return true
      if (this.name == null || this.name == '') return true
      if (this.numOfPpl == 0) return true
      return false
    },
    getLink() {
      if (this.active == 1) {
        if (this.haveInvalidField()){
          this.$message.error(this.isEnglish ? 'Oops, Please key in all field.' : 'ËØ∑Â°´ÂÜôÊâÄÊúâËµÑÊñô' )
          return
        }
        location.href = `https://wa.me/60126549696?text=${this.getWhatsappMsg()}`
      }
    },
    getWhatsappMsg() {
      const nextLine="%0A"
      let result = `ÊÇ®Â•Ω Madam Tan, ÊàëÊòØ${this.name}. ËøôÊòØ‰ªéÁΩëÁ´ôorderÔºö`
      result += nextLine
      result += `ËØ≠Ë®Ä: ${this.languagePreference}`
      result += nextLine
      result += `ÁßçÁ±ª: ${this.type}`
      result += nextLine
      result += `Êó∂Èó¥: ${moment(this.date).format('YYYY-MMM-DD (ddd)')} ${moment(this.time, 'HH:mm').format('hh:mma')}`
      result += nextLine
      result += `‰∫∫Êï∞: ${this.numOfPpl}`
      result += nextLine
      result += `----- ËèúÂçï -----`
      result += nextLine
      if (this.orders.length > 0) {
        this.orders.forEach(order => {
          let found = foods.find(el => el.id == order.id)
          let price = found.price ? found.price[order.size] : 'Êó∂‰ª∑'
          result += `${price} -> ${found.name.chinese}`
          result += nextLine
        })
      } else {
        result += `Ê≤°Ê∑ªÂä†‰ªª‰ΩïÈ£üÁâ©`
      }
      return result
    },
    parseSize(size) {
      if (size == 'small') return this.isEnglish ? 'S' : 'Â∞è'
      else if (size == 'medium') return this.isEnglish ? 'M' : '‰∏≠'
      else if (size == 'big') return this.isEnglish ? 'B' : 'Â§ß'
      else return this.isEnglish ? 'Y' : 'Ë¶Å'
    }
  },
  computed: {
    isEnglish() {return this.language == 'english'},
  },
  watch: {
  },
}
</script>

